

export const displayNotification = (text) => {
    if (Notification.permission == 'granted') {
        navigator.serviceWorker.getRegistration().then((reg) => {
            let options = {
                body: text,
                icon: 'favicon.ico',
                vibrate: [100, 50, 100],
                actions: [
                    {
                        action: 'close', title: 'Ð—Ð°ÐºÑ€Ñ‹Ñ‚ÑŒ'
                    }
                ]
            }
            reg.showNotification('Template task', options)
        })
    }
}